<template>
  <div id="app">
    <component-header></component-header>
    <component-menu></component-menu>
    <component-main></component-main>
    <component-footer></component-footer>
  </div>
</template>

<script>
import componentHeader from './components/common/header'
import componentMenu from './components/common/menu'
import componentMain from './components/common/main'
import componentFooter from './components/common/footer'
export default {
  name: 'App',
  components: {
    'componentHeader': componentHeader,
    'componentMenu': componentMenu,
    'componentMain': componentMain,
    'componentFooter': componentFooter
  }
}
</script>

<style>
html {
  height: 100%;
}
body {
  margin: 0px;
  height: 100%;
}
#app {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



